.header {
  background-color: #252525;
  margin: 0 0 20px 0;
}

.header .logo {
  float: left;
  position: relative;
}

.header .menu {
  float: right;
}

.header .logo .title {
  display: inline-block;
  position: absolute;
  top: 16px;
  color: white;
}

.header .logo .image {
  display: inline-block;
  width: 80px;
  -webkit-animation: spin infinite 15s linear;
          animation: spin infinite 15s linear;
}

.header .menu .option {
  position: relative;
  top: 20px;
  margin: 0 10px;
  text-decoration: none;
}


