* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', 'monospace', sans-serif;
  background-color: ghostwhite;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  color: white;
}

.wrapper {
  width: 90%;
  margin: 0 auto;
}

.sn-row {
  width: 100%;
  padding: 10px;
  background-color: #F1F1F1;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.sn-row .title {
  padding: 0 0 10px 0;
}

.sn-row input[type='text'],
.sn-row input[type='email'],
.sn-row input[type='password'],
.sn-row textarea {
  width: 100%;
  margin: 5px 0;
  padding: 4px;
  font-size: 16px;
}

.sn-row input[type='submit'] {
  margin: 8px 0 0 0;
  width: 100%;
}

.btn {
  padding: 8px 10px;
  border: none;
  background-color: dodgerblue;
  color: white;
  font-size: 17px;
  cursor: pointer;
}

.btn:hover {
  background-color: deepskyblue;
}

.card {
  background-color: #F1F1F1;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.cf:after {
  content: '';
  display: block;
  clear: both;
}

@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



